import { useState } from "react";
import { Button } from "semantic-ui-react";

import { isAbilityActive } from '../utils';
import FighterCard from "./FighterCard";
import AbilityCard from "./AbilityCard";
import { ability_comparator } from './referenceSheetUtils';

import './ReferenceSheet.css';

const Preview = ({group, printOptions}) => {
	const [showPreview, setShowPreview] = useState(false);

	const fighters = group.fighters.filter(
		(fighter) => group.selectedFighters[fighter.id]
	);

	const abilities = group.abilities.filter(
		(ability) => isAbilityActive(ability, fighters, group.abilityOverrides)
	);
	abilities.sort(ability_comparator);

	return (
		<div className="ReferenceSheet no-print mt-10">
			{ (fighters.length > 0 || abilities.length > 0) &&
				<Button onClick={() => setShowPreview(!showPreview)}>
					{showPreview? "Hide Preview" : "Show Preview"}
				</Button>
			}
			{ showPreview &&
				<>
					{ fighters.length > 0 &&
						<>
							<h3 >Fighters</h3>
							<div className="page card-list">
								{fighters.map((fighter) => (
									<FighterCard
										key={fighter.id}
										fighter={fighter}
										printOptions={printOptions} />
								))}
							</div>
						</>
					}
					{ abilities.length > 0 &&
						<>
							<h3 >Abilities</h3>
							<div className="page card-list">
								{abilities.map((ability) => (
									<AbilityCard key={ability.id} ability={ability} />
								))}
							</div>
						</>
					}
				</>
			}
		</div>
	);
}

export default Preview;
