import { useMemo } from 'react';

import {objectMapToArray} from '../utils';

import './FighterSelector.css';

const FighterSelector = ({fighters, selectedFighters, setSelectedFighters}) => {
	const onFighterCountChanged = (id) => (e) => {
		const newValue = +e.target.value;
		if (newValue >= 0) {
			setSelectedFighters({
				...selectedFighters,
				[id]: +e.target.value
			});
		}
	}

	const totalPoints = objectMapToArray(selectedFighters, ([key, value]) => 
		((fighters.find(
			(fighter) => +(fighter.id) === +(key)
		) || 0)?.points || 0) * value
	).reduce((a,b) => a + b, 0);

	const heroCount = useMemo(() => {
		let heroCount = 0;
		for (const fighterId in selectedFighters) {
			const fighterCount = selectedFighters[fighterId];
			const fighter = fighters.find(
				(fighter) => +(fighter.id) === +(fighterId))
			if (
				fighter && 
				fighterCount &&
				fighter.runemarks.includes('fighters-leader')
			) {
				heroCount += fighterCount;
			}
		}
		return heroCount;
	}, [fighters, selectedFighters]);

	const normalStyle = {color: "black"};
	const warningStyle = {color: "red"};

	return (
		<div className="FighterSelector no-print">
			<div style={totalPoints > 1000 ? warningStyle : normalStyle}>
				{totalPoints}{"/1000 points"}
			</div>
			<div style={(heroCount < 1 || heroCount > 3) ?
				warningStyle : normalStyle}>
				{heroCount}{"/3 heroes"}
			</div>
			{fighters.map((fighter) => (
				<div key={fighter.id}>
					<input
						value={selectedFighters[fighter.id] || 0}
						type="number"
						onChange={onFighterCountChanged(fighter.id)} />
					{fighter.name} ({fighter.points} points)
				</div>
			))}
		</div>
	);
}

export default FighterSelector;
