export class PrintLayout {
	static SEPARATE_VERTICAL = new PrintLayout(
		"SEPARATE_VERTICAL", "Separate Fighters and Abilities");
	static DENSE_VERTICAL = new PrintLayout(
		"DENSE_VERTICAL", "Fewest Pages");

	static layouts = [
			PrintLayout.SEPARATE_VERTICAL,
			PrintLayout.DENSE_VERTICAL,
	]

	static getPrintLayout = (printOptions) => {
		return PrintLayout.layouts.find(
			(layout) => layout.value === printOptions?.layout
		) ?? PrintLayout.SEPARATE_VERTICAL;
	}


	constructor(value, presentationName) {
		this.value = value;
		this.presentationName = presentationName;
	}
}

export const shouldHideDropShadow = (printOptions) => {
	return printOptions.hideDropShadow ?? false;
}
