import { isAbilityActive } from '../utils';
import { PrintLayout } from '../state/printOptions';
import { ability_comparator } from './referenceSheetUtils';
import FighterCard from "./FighterCard";
import AbilityCard from "./AbilityCard";

import './ReferenceSheet.css';

const ReferenceSheet = ({group, printOptions}) => {
	const fighters = group.fighters.filter(
		(fighter) => group.selectedFighters[fighter.id]
	);

	const abilities = group.abilities.filter(
		(ability) => isAbilityActive(ability, fighters, group.abilityOverrides)
	);
	abilities.sort(ability_comparator);

	const fighterCards = fighters.map((fighter) => (
		<FighterCard
			key={`${fighter.id}`}
			fighter={fighter}
			printOptions={printOptions} />
	));

	const abilityCards = abilities.map((ability) => (
		<AbilityCard
			key={`ability-${ability.id}`}
			ability={ability}
			printOptions={printOptions} />
	));

	const printLayout = PrintLayout.getPrintLayout(printOptions);

	switch (printLayout) {
		case PrintLayout.SEPARATE_VERTICAL:
			return (
				<div className="ReferenceSheet print-only">
					<div className="page card-list">
						{fighterCards}
					</div>
					<div className="page card-list">
						{abilityCards}
					</div>
				</div>
			);
		case PrintLayout.DENSE_VERTICAL:
			return (
				<div className="ReferenceSheet print-only">
					<div className="page card-list">
						{fighterCards}
						<div className="mt-20" />
						{abilityCards}
					</div>
				</div>
			);
		default:
			return (
				<div></div>
			);
	}
}

export default ReferenceSheet;
