import "./Roster.css";

const Roster = ({
	abilities,
	fighters,
	setCurrentlyEditing,
	deleteFighter,
	deleteAbility
}) => {
	const setEditingFighter = (fighter) => setCurrentlyEditing({
		type: "FIGHTER",
		value: fighter
	});

	const setEditingAbility = (ability) => setCurrentlyEditing({
		type: "ABILITY",
		value: ability
	});

	if (fighters.length || abilities.length) {
		return (
			<div className="no-print Roster">
				<div className="column">
					<h3>Fighters</h3>
					{ fighters.map((fighter) => (
						<div key={fighter.id} className="resource-row">
							<button
								className="edit icon-button"
								onClick={() => setEditingFighter(fighter)} />
							<button 
								className="delete icon-button"
								onClick={() => deleteFighter(fighter.id)} />
							<div className="resource-name">
								{fighter.name}
							</div>
						</div>
					  ))
					}
				</div>
				<div className="column">
					<h3>Abilities</h3>
					{ abilities.map((ability) => (
						<div key={ability.id} className="resource-row">
							<button 
								className="edit icon-button"
								onClick={() => setEditingAbility(ability)} />
							<button 
								className="delete icon-button"
								onClick={() => deleteAbility(ability.id)} />
							<div className="resource-name">
								{ability.name}
							</div>
						</div>
					  ))
					}
				</div>
			</div>
		);
	} else {
		return;
	}
}

export default Roster;
