import {weaponRunemarkCategory} from "../../Runemarks/runemarkList";
import Runemark from '../../Runemarks/Runemark';
import RunemarkSelector from "../../Runemarks/RunemarkSelector";

const WeaponEditor = ({weapon, onChange, onDelete}) => {

	const onValueChange = (value) => (e) => {
		onChange({
			...weapon,
			[value]: e.target.value
		});
	}

	const onRunemarkSelected = (runemark) => {
		onChange({...weapon, runemark});
	}

	// TODO: Styling probably needs to be more compact?
	// Make it look like it does on the Fighter card??
	return (
		<div className="WeaponEditor">
			<RunemarkSelector
				runemarkCategories={weaponRunemarkCategory}
				onRunemarkSelected={onRunemarkSelected}>
				<button className="WeaponEditor-runemark-button">
					<Runemark runemark={weapon.runemark} />
				</button>
			</RunemarkSelector>
			<label htmlFor="range">Range:</label>
			<input
				id="range"
				className="range-input"
				value={weapon.range}
				onChange={onValueChange("range")}/>
			<label htmlFor="attacks">Attacks:</label>
			<input
				id="attacks"
				value={weapon.attacks}
				onChange={onValueChange("attacks")}/>
			<label htmlFor="strength">Strength:</label>
			<input
				id="strength"
				value={weapon.strength}
				onChange={onValueChange("strength")}/>
			<label htmlFor="normal_damage">Damage:</label>
			<input
				id="normal_damage"
				value={weapon.normal_damage}
				onChange={onValueChange("normal_damage")}/>
			{"/"}
			<input
				id="critical_damage"
				value={weapon.critical_damage}
				onChange={onValueChange("critical_damage")}/>
			<button
				className="delete icon-button"
				onClick={onDelete} />
		</div>
	);
};

export default WeaponEditor;
