import { useState } from 'react';
import { Button, Modal } from 'semantic-ui-react';


const GroupImportExport = ({currentGroup, updateGroup, getNextGroupId}) => {
	const [importModalOpen, setImportModalOpen] = useState(false);
	const [importText, setImportText] = useState("");

	const encode = (s) =>
		btoa(unescape(encodeURIComponent(JSON.stringify(s))));
	const decode = (s) =>
		JSON.parse(decodeURIComponent(unescape(atob(s))));

	const exportGroup = () => {
		navigator.clipboard.writeText(encode(currentGroup));
		//TODO: replace this with a Toast or something.
		alert(`${currentGroup.name} copied to clipboard.`);
	}

	const importGroup = (groupEncodedString) => {
		const importedGroup = decode(importText);
		const id = getNextGroupId();
		importedGroup.id = id;
		updateGroup(importedGroup);
		setImportText("");
		setImportModalOpen(false);
	}

	return (
		<>
			<Modal
				trigger={
					<button className="icon-button" title="Import Roster">
						
					</button>
				}
				open={importModalOpen}
				onOpen={() => setImportModalOpen(true)}
				onClose={() => setImportModalOpen(false)}>
				<Modal.Header>
					Import Roster
				</Modal.Header>
				<Modal.Content>
					<input
						value={importText}
						onChange={(e) => setImportText(e.target.value)}/>
				</Modal.Content>
				<Modal.Actions>
					<Button onClick={importGroup}>
						Import
					</Button>
				</Modal.Actions>
			</Modal>
			{ currentGroup &&
				<button className="icon-button"
					onClick={exportGroup}
					title="Export Roster">
					
				</button>
			}
		</>
	);
}

export default GroupImportExport;
